* {
  margin: 0px;
  padding: 0;
  box-sizing: border-box;
}

.title {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 100px;
  /* background-image: linear-gradient(to right, green, yellow); */
  height: 30vh;
  text-shadow: -6px 2px 6px;
}

#formid {
  display: flex;
  width: 100vw;
  justify-content: space-around;
  align-items: center;
  /* border: 2px solid red; */
  padding-left: 200px;
  padding-right: 200px;
}
#formid input,
#formid button {
  text-align: center;
  height: 50px;
  border-radius: 5px;
  width: 200px;
  box-shadow: -2px 2px 10px 5px lightgray;
}
button {
  background: white;
}

.footer {
  text-align: center;

  text-shadow: -2px 2px 2px;
  font-size: 40px;
}
